import React ,{useEffect, useState} from "react";
import {useHistory ,useParams} from 'react-router-dom'
import styled from "styled-components";
import './Detail.scss';

let 박스 = styled.div`
    padding:20px;
`;

let 제목 = styled.h4`
    font-size:25px;
    color:${props => props.색상}
`;

function  Detail(props) {
  let [경고창,경고창변경] = useState(true);
  let [인풋,인풋변경]=useState("");
  
  useEffect(()=>{
   let 타이머 = setTimeout(()=>{
    경고창변경(false);
    },2000)
    return()=>{clearTimeout(타이머)} //디테일 컴포넌트가 사라질때 실행할 함수
  },[])

    let history = useHistory();
    let {id} =useParams();

    let 찾은상품 = props.shoes.find((상품)=>{
        return 상품.id == id
    })
    
    return(
      <div className="container">
          <박스><제목 className="red">detail</제목></박스>
          {
            경고창 == true
            ? (<div className="my-alert-2">
              <p>재고가 얼마 남지 않았어요!</p>
             </div>)
            : null
          }
          {인풋}
         <input onChange={(e)=>{인풋변경(e.target.value)}}/>
            <div className="row">
              <div className="col-md-6">
                <img src={"https://codingapple1.github.io/shop/shoes"+(parseInt(id)+1)+".jpg"} width="100%" />
              </div>
              <div className="col-md-6 mt-4">
                <h4 className="pt-5">{찾은상품.title}</h4>
                <p>{찾은상품.content}</p>
                <p>{찾은상품.price}</p>

                <Info 재고={props.재고} id={id} />

                <button className="btn btn-danger" onClick={()=>{

                  let 뉴재고 =[...props.재고]

                  
                  뉴재고[id] = 뉴재고[id]-1

                  props.재고변경(뉴재고) //마이너스까지 안나오게 하는 방법.?

                }}>주문하기</button> 
                <button className="btn btn-danger" onClick={()=>{
                    history.goBack();
                    // history.push('/') /로 가주세요
                }}>뒤로가기</button> 
              </div>
            </div>
      </div> 
    )
  }

  function Info(props){
    return(<p>재고:{props.재고[props.id]}</p>)
  }

export default Detail;